import {useHttpClient} from "../hooks/http-hook";
import {AuthContext} from "../context/auth-context";
import {useContext, useEffect, useState} from "react";
import {Storage} from "../../utility/Storage";
import Modal from "../UIElements/Modal";
import Button from "../UIElements/Button";
import {Hr} from "../UIElements/Hr";

export const Analytics = (props) => {
    const session = new Storage('session');
    const [show, setShow] = useState(false);
    const {sendRequest} = useHttpClient();
    const {sessionId, setUpAnalytics} = useContext(AuthContext);

    const getIsExpired = (createdAt) => {
        const createdAtValue = new Date(createdAt);
        const createdValueOf = createdAtValue.setHours(createdAtValue.getHours() + 1);
        const nowValue = (new Date()).valueOf()

        return nowValue > createdValueOf.valueOf();
    }

    useEffect(() => {
        if (!sessionId) {
            const sess = session.get();

            if (!sess) {
                setTimeout(() => setShow(true), 4000)
            }
        }
    }, [])

    const manageAnalytics = async (isAccepted) => {
        try {
            const sessionId = await sendRequest(`${process.env.REACT_APP_ENDPOINT}analytics`);

            setUpAnalytics(sessionId, isAccepted);

            setShow(false);
        } catch (e) {
            console.log(e);
            setShow(false);
        }
    }

    return <Modal
        show={show}
        onCancel={() => setShow(false)}
        className={''}
    >
        <h2 className={'fs-22 pb-2'}>Az n adatainak vdelme fontos a szmunkra</h2>
        <p className={'fs-18 text-align-left pb-2'}>Mi s a partnereink informcikat  pldul stiket  trolunk egy
            eszkzn vagy
            hozzfrnk az eszkzn
            trolt informcikhoz, s szemlyes adatokat  pldul egyedi azonostkat s az eszkz ltal kldtt
            alapvet informcikat  kezelnk szemlyre szabott hirdetsek s tartalom nyjtshoz, hirdets- s
            tartalommrshez, nzettsgi adatok gyjtshez, valamint termkek kifejlesztshez s a termkek
            javtshoz.Az n engedlyvel mi s a partnereink eszkzleolvassos mdszerrel szerzett pontos geolokcis
            adatokat s azonostsi informcikat is felhasznlhatunk. A megfelel helyre kattintva hozzjrulhat ahhoz,
            hogy mi s a partnereink a fent lertak szerint adatkezelst vgezznk.
            Felhvjuk figyelmt, hogy szemlyes adatainak bizonyos kezelshez nem felttlenl szksges az n
            hozzjrulsa, de jogban ll tiltakozni az ilyen jelleg adatkezels ellen. A belltsai csak erre a
            weboldalra rvnyesek. Krjk olvassa el <a
                className={'text-decoration-none hover-opacity'}
                href={'/gdpr'}>adatvdelmi
                szablyzatunkat</a></p>
        <Hr type={'grey'}/>
        <div className={'display-flex'}>
            <Button onClick={() => manageAnalytics(false)} buttonClass='btn--outline w-px-200'>Nem fogadom el</Button>
            <Button onClick={() => manageAnalytics(true)} buttonClass='btn--green w-px-200'>Elfogadom</Button>
        </div>
    </Modal>
}
