import React, {useContext, useEffect, useState} from 'react';
import {ExpiryContext} from '../../shared/context/expiry-context';
import {useHttpClient} from '../../shared/hooks/http-hook';


const Testimonial = () => {
    const {testimonialExpiry} = useContext(ExpiryContext);
    const [mobile, setMobile] = useState('');
    const [testimonial, setTestimonial] = useState({
        quote: 'A fzs is mvszet',
        text: ''
    })

    const {sendRequest} = useHttpClient()
    /* watch for screen size and use size appropiate images */
    const resizeWatcher = e => {
        if (e.target.outerWidth > 700) {
            setMobile('')
        } else {
            setMobile('-mobile')
        }
    }
    useEffect(() => {
        window.addEventListener("resize", resizeWatcher);
    })
    useEffect(() => {
        (async () => {
            try {
                const responseData = await sendRequest(process.env.REACT_APP_QUOTE)
                setTestimonial({
                    quote: responseData.testimonial.quote,
                    text: responseData.testimonial.text
                })
                localStorage.setItem('testimonial', JSON.stringify(responseData.testimonial));
            } catch (err) {

            }
        })()
    }, [testimonialExpiry])

    return (
        <div className='testimonial'>
            <div className='testimonial__left'>
                <h2 className='heading-secondary'>"{testimonial.quote}"</h2>
                <h4 className='heading-italic'>Christian Martinez Sf</h4>
                <p className='paragraph paragraph--testimonial pr-4'>{testimonial.text.length > 0 ? testimonial.text :
                    ' egyedi, vendgeink ltal sokat dicsrt zvilgval igyekszik a gasztronmia szerelmeseinek kedvben jrni.ttermnkben igyeksznk ' +
                    'helyi alapanyagokbl, a krnykre jellemz zekkel megismertetni vendgeinket. Bszkk vagyunk arra, hogy konyhnkon a helyben tertkre ' +
                    'hozott vadbl frissen, sousvidelt s smooker BBQ telek kszlnek. Szezonlisan igyeksznk bemutatni a tjegysg zeit. Leveseket, fteleket' +
                    's helyben kszlt desszertek formjban.teleink elksztse sorn igyeksznk kiszolglni az egyedi ignyeket, elvarzsoljuk klnbz nemzetek' +
                    'konyhinak zeit figyelembe vve vendgeink esetleges tel rzkenysgeit is.'}</p>

            </div>

            <div className='testimonial__right'>
                <div className='image-container'>
                    <img src={`/img/chef${mobile}.jpg`} alt='Christian Martinez' className='chef-image'/>
                </div>


            </div>
        </div>
    )
}


export default Testimonial;
