import { UserDetails } from "../components/UserDetails";
import { useContext, useEffect, useState } from 'react';
import { useHttpClient } from '../../shared/hooks/http-hook';
import { MiscData } from '../components/MiscData';
import { DatePicker } from "../components/DatePicker";
import { Hr } from "../../shared/UIElements/Hr";
import { OrderContext } from "../../shared/context/order-context";
import MessageModal from "../../shared/UIElements/MessageModal";
import { redirect } from "../../utility/helpers";
import { ItemsPicked } from "../components/ItemsPicked";
import { Storage } from '../../utility/StorageHelper';
import { get } from "../../shared/helpers/util";
const propsToCheckForValidity = ['userData', 'pickup', 'misc']
export const Checkout = props => {
    const { addedItems, clearOrder } = useContext(OrderContext)
    const storage = new Storage('uniqueOrderId');
    const [isFormValid, setIsFormValid] = useState(false);
    const [data, setData] = useState({
        userData: {},
        pickup: '',
        misc: {},
        uniqueId: storage.get()?.uniqueId,
    });
    const getValues = (values, prop, isValid) => {
        setData({
            ...data,
            [prop]: { values, isValid },
        })
    }

    // TODO -> Figure the form validity logic. something is wrong we do not get the data properly from the data
    // object.
    useEffect(() => {
        let falsy = 0;
        for (const prop in data) {
            if (data.hasOwnProperty(prop) && propsToCheckForValidity.includes(prop)) {
                if (!data[prop].isValid) {
                    falsy += 1;
                }
            }
        }

        setIsFormValid(!falsy);
    }, [data])

    // TODO -> card to display orderd items
    return <div className='full-screen max-width-vw-100 m-md-3 mt-14 display-flex align-items-center flex-column'>
        <div className='w-px-800 py-2 max-width-vw-98'>
            <div className=''>
                <h2 className='fs-22 fw-800'>Kivlasztott telek</h2>
                <ItemsPicked />
            </div>
            <div className=''>
                <h2 className='fs-22 fw-800'>Rendels tvevje</h2>
                <UserDetails getValues={(values, isValid) => getValues(values, 'userData', isValid)} />
            </div>
            <Hr className={'margin-auto'} type={'light'} size={80} />
            <div className='display-flex justify-content-start flex-column align-items-baseline'>
                <h2 className='fs-22 fw-800'>Rendels tvtelnek idpontja</h2>
                <DatePicker getValues={(values, isValid) => getValues(values, 'pickup', isValid)} />
            </div>
            <Hr className={'margin-auto'} type={'light'} size={80} />
            <div className=''>
                <h2 className='fs-22 fw-800'>Egyb</h2>
                <MiscData getValues={(values, isValid) => getValues(values, 'misc', isValid)} />
            </div>

            <div className='position-center'>
                <OrderButton
                    isFormValid={isFormValid}
                    getData={() => new OrderObject({ ...data, addedItems })} onSuccess={clearOrder}
                />
            </div>
        </div>
    </div>;
}


// TODO -> this button to be for the next page.
const OrderButton = props => {
    const { sendRequest, error, clearError } = useHttpClient();
    const [message, setMessage] = useState('');
    const [customError, setCustomError] = useState('');
    const [redir, setRedir] = useState(false);
    // TODO -> Make sure they checked out the aszf and gdpr boxes...
    const order = async () => {
        try {
            const data = JSON.stringify(props.getData(), null, {});

            if (!props.isFormValid) {
                throw new Error('Krlek gyzdj meg rla, hogy helyes adatokat adtl meg.');
            }

            const responseData = await sendRequest(
                process.env.REACT_APP_PLACE_ORDER,
                'POST',
                data,
                { 'Content-Type': 'application/json' }
            )
            if (!responseData) {
                throw new Error('Hiba trtnt! Krem prblja jra ksbb');
            }
            if (props.onSuccess) {
                props.onSuccess();
            }

            setMessage(get(responseData, 'message', 'Ksznjk a rendelsed'))
        } catch (err) {
            switch (get(err, 'message', '')) {
                case 'FATAL_ERROR':
                    setCustomError('Sajnljuk de a rendels leads nem sikertlt, krnk prbld jra.')
                    setRedir(true);
                    break;
                default:
                    setCustomError(get(err, 'message', 'Sajnljuk de a rendels leads nem sikertlt, krnk prbld jra.'));
                    break;
            }
        }
    }

    return <div>
        <MessageModal
            onClear={() => redirect('/')}
            message={message}
            className='admin-message-modal'
        />
        <MessageModal
            onClear={() => {
                setCustomError('');
                if (redir) {
                    redirect('/')
                }
            }}
            message={customError}
            className='admin-error-modal'
        />
        <button
            className='order-button'
            onClick={order}
        >
            <span>Megrendelem</span>
        </button>
    </div>
}


class OrderObject {
    items;
    pickupDate;
    name;
    email;
    phone;
    tax;
    gdpr;
    aszf;
    note;
    uniqueId;
    constructor(data) {
        this.items = data.addedItems.map(i => ({ id: i._id, amount: i.amount }));
        this.name = getData(data, 'userData.values.inputs.name.value', 'Krnk add meg a nevedet');
        this.phone = getData(data, 'userData.values.inputs.phone.value', 'Krnk add meg a telefon szmodat');
        this.email = getData(data, 'userData.values.inputs.email.value', 'Krnk add meg az email cmed');
        this.pickupDate = getData(data, 'pickup.values', 'Krnk vlassz tvteli id pontot');
        this.tax = !!getData(data, 'misc.values.checkboxes.needTax.value', '', false);
        this.gdpr = getData(data, 'misc.values.checkboxes.gdpr.value', 'Nem egyeztl bele az adatvdelmi szablyzatunkba');
        this.aszf = getData(data, 'misc.values.checkboxes.aszf.value', 'Nem egyeztl bele az ltalnos szerzdsi felttelekbe');
        this.note = getData(data, 'misc.values.note', '', false);
        this.uniqueId = getData(data, 'uniqueId', 'FATAL_ERROR');
    }
}

const getData = (baseObject, prop, errorMessage, isRequired = true) => {
    const value = get(baseObject, prop, '')
    if (!!value) {
        return value;
    }

    if (isRequired) {
        throw new Error(errorMessage, 400)
    }

    return '';
}